.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 10;
    background: rgba(0, 0, 0, 0.7);
  }
  
  .modal {
    position: fixed;
    top: 30vh;
    left: 10%;
    width: 70%;
    z-index: 100;
    overflow: hidden;
    background: #333;
  }
  
  .header {
    background: #2d3250;
    padding: 2rem;
  }
  
  .header h2 {
    margin: 0;
    color: white;
  }
  
  .content {
    padding: 2rem;
  }
  
  .actions {
    padding: 2rem;
    display: flex;
    justify-content: flex-end;
  }
  
  @media (min-width: 768px) {
    .modal {
      left: calc(50% - 25rem);
      width: 50rem;
    }
  }

